<template>
    <div>员工资料
        <el-button type="primary" @click="start">彩蛋</el-button>
        <el-input prefix-icon="el-icon-edit" size="small" style="width: 400px;margin-left: 20px"
                  v-model="depJobDemo[user.username]"></el-input>
    </div>
</template>

<script>
    import {mapState} from "vuex";

    export default {
        name: "PerEmp",
        data() {
            return {
                user:JSON.parse(window.sessionStorage.getItem("user"))
            }
        },
        methods: {
            start() {
                this.$store.state.stomp.send('/ws/job');   //两种方式均可，只要发到对应的接口即可执行定时更新任务
                // this.getRequest("/ttt/job").then(resp=>{   //以这种方式 时候不要以/ws开头，不然会以ws协议发出那就找不到了
                //
                // })
            }
        },
        computed: mapState([
            'depJobDemo'
        ]),
    }
</script>

